/* begin custom styles */

/* ReactCSSTransitionGroup custom styles */

.enter {
  -webkit-transform: translateY(-100%) !important;
      -ms-transform: translateY(-100%) !important;
          transform: translateY(-100%) !important;
  opacity: 1 !important;
}

.enter.enter-active {
  -webkit-transform: translateY(0%) !important;
      -ms-transform: translateY(0%) !important;
          transform: translateY(0%) !important;
  -webkit-transition: -webkit-transform 1500ms ease 0s !important;
  transition: -webkit-transform 1500ms ease 0s !important;
  -o-transition: transform 1500ms ease 0s !important;
  transition: transform 1500ms ease 0s !important;
  transition: transform 1500ms ease 0s, -webkit-transform 1500ms ease 0s !important;
  opacity: 1 !important;
}

.peace {
    -webkit-transform: translateY(-100%) !important;
        -ms-transform: translateY(-100%) !important;
            transform: translateY(-100%) !important;
    opacity: 1 !important;
}

.peace.peace-active {
  -webkit-transform: translateY(0%) !important;
      -ms-transform: translateY(0%) !important;
          transform: translateY(0%) !important;
  -webkit-transition: -webkit-transform 1500ms ease 0s !important;
  transition: -webkit-transform 1500ms ease 0s !important;
  -o-transition: transform 1500ms ease 0s !important;
  transition: transform 1500ms ease 0s !important;
  transition: transform 1500ms ease 0s, -webkit-transform 1500ms ease 0s !important;
  opacity: 1 !important;
}

