button.type, button.category {
    padding-left: 2.5rem;
    background-color: rgba(255, 255, 255, 0);
    color: #FFFFFF;
    width: 100%;
    left: -4rem;
    text-align: left;
    -webkit-transition: 0.5s 0s ease all;
    -o-transition: 0.5s 0s ease all;
    transition: 0.5s 0s ease all;
    font-family: 'Raleway', sans-serif;
    font-size: 0.8rem;
    outline: none;
    border: 0;
    cursor: pointer;
}
button.type.selected, button.category.selected {
    background-color: #FFFFFF;
    color: #1E1B1B;
}
button.type.unselected, button.category.unselected {
    background-color: rgba(255, 255, 255, 0);
    color: #FFFFFF;
}
button.timeEvent {
    background-color: #000;
    background: -webkit-radial-gradient(circle, rgba(0, 0, 0, 1) 30%, rgba(0, 0, 0, 0) 50%);
    background: -o-radial-gradient(circle, rgba(0, 0, 0, 1) 30%, rgba(0, 0, 0, 0) 50%);
    background: radial-gradient(circle, rgba(0, 0, 0, 1) 30%, rgba(0, 0, 0, 0) 50%);
    border: 0;
    border-radius: 50%;
    width: 100%;
    min-width: 0.5rem;
    height: 100%;
    min-height: 0.5rem;
    outline: none;
    padding: 0;
}
button.timeEvent.no-match {
    -webkit-transform: scale(0.7, 0.7);
        -ms-transform: scale(0.7, 0.7);
            transform: scale(0.7, 0.7);
    opacity: 0.2;
    -webkit-transition: all 1s ease;
    -o-transition: all 1s ease;
    transition: all 1s ease;
}
button.timeEvent.match {
    -webkit-transform: scale(1.5, 1.5);
        -ms-transform: scale(1.5, 1.5);
            transform: scale(1.5, 1.5);
    opacity: 1;
    -webkit-transition: all 1s ease-out;
    -o-transition: all 1s ease-out;
    transition: all 1s ease-out;
}
